.bubbles span {
  position: relative;
  width: 30px;
  height: 30px;
  background: #4fc3dc;
  margin: 0 4px;
  border-radius: 50%;
  box-shadow: 0 0 0 10px #4fc3dc44, 0 0 50px #4fc3dc, 0 0 100px #4fc3dc;
  animation: animate 15s linear infinite;
  animation-duration: calc(100s/var(--i));
}

.bubbles span:nth-child(even) {
  background: #ff2d75;
  box-shadow: 0 0 10px #ff2d7544, 0 0 50px #ff2d75, 0 0 100px #ff2d75;

}

@keyframes animate {
  0% {
    transform: translateY(100vh) scale(0);
  }

  100% {
    transform: translateY(-0100vh) scale(1);
  }
}

.section-title1 {
  font-size: 1.8rem;
  color: #7A431D;
  font-weight: 1000;
  text-align: center;
  margin-bottom: 1.8rem;
}

.shop1 {
  margin-top: 2rem;
}

.shop-content1 {
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

section1 {
  padding: 4rem 0 3rem;
}

.product-img1 {
  height: 9.5rem;
  border-radius: 50%;
  margin-bottom: 0.2rem;
  padding: 10px;
  /* width: 90%; */
  width: 150px;
}

.product-box1 {
  background: rgb(254, 250, 250);
  border-radius: 5px;
  /* flex-grow: 1;
    flex-basis: 0;*/
  margin: 1rem;
  height: auto;
  width: 380px;
  padding: 0.25rem;
  border: 2px solid black;
  display: flex;
  text-decoration: none;
}

.product-title1 {
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 0.8rem;
  color: black;
}

.price1 {
  font-weight: 500;
  color: rgb(6, 6, 6);
}

.Pincode1 {
  border: 2px solid red;
}

.product-box1:hover {
  background: #858380;
  border: 3px solid black;
  box-shadow: 0 8px 32px hsla(0.75, 0.82, 0.165, 1);
  transition: 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.product-box1:hover .product-title,
.product-box1:hover .price {
  color: #141414;
}

.product-box1:hover .img {
  border: 3px solid black;
}

.Image-box1 {
  background-color: #e0d9d9;
  margin-right:10px;
}
