.contain {
  position: relative;
  z-index: 0;
  width: 90%;
  margin: 0 auto;
  padding: 2rem;
  background-color: #e6d9ca;
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgb(32, 32, 32);
  border-radius: 5px;
  display: grid;
}

